// 查询评论
export function queryComments(config = {}) {
  const { startDate, endDate } = config;
  const conditions = generateQuerys('Comment', { startDate, endDate });
  const query = AV.Query.and(...conditions);

  return new Promise((resolve, reject) => {
    query.find().then(
      (res) => resolve(res),
      (err) => reject(err),
    );
  });
}

// 统计近n天pv
export function countlatestDaysPV(n = 7) {
  const days = latestNDays(n);
  const results = [];

  days.forEach((day, idx) => {
    const { start, end } = day;
    const startDate = start.format();
    const endDate = end.format();

    const result = countPV({
      startDate,
      endDate,
    });

    results.push(
      result
        .then((pv) => {
          return {
            startDate,
            endDate,
            date: start.format('YYYY-MM-DD'),
            pv: pv,
          };
        })
        .catch((err) => {
          console.error('统计pv错误：', err);
        }),
    );
  });

  return Promise.all(results).then((pvList, idx) => {
    return pvList;
  });
}

// 统计近n天评论
export function countLatestDaysComments(n = 7) {
  const days = latestNDays();
  const results = [];

  days.forEach((day) => {
    const { start, end } = day;
    const startDate = start.format();
    const endDate = end.format();

    const result = countComments({
      startDate,
      endDate,
    });

    results.push(
      result
        .then((count) => ({
          startDate,
          endDate,
          date: start.format('YYYY-MM-DD'),
          count,
        }))
        .catch((err) => {
          console.error('统计评论错误：', err);
        }),
    );
  });

  return Promise.all(results).then((commentCounts, idx) => {
    return commentCounts;
  });
}
