import React, { useEffect, useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import RefreshIcon from '@material-ui/icons/Refresh';
import { makeStyles } from '@material-ui/core/styles';
import LineChart from '../../components/Chart/LineChart';

import { countlatestDaysPV, countLatestDaysComments, countPV, countComments } from '../../helper';

import './index.scss';

const useStyles = makeStyles((theme) => ({
  refreshBtn: {
    position: 'absolute',
    right: 0,
    top: 0,
  },
}));

export default function Dashboard() {
  const [allPVCount, setAllPVCount] = useState(0);
  const [allCommentCount, setAllCommentCount] = useState(0);
  const [pvsCounts, setPVsCount] = useState([]);
  const [commentsCount, setCommentsCount] = useState([]);
  const [refreshKey, setRefreshKey] = useState(null);
  const classes = useStyles();

  useEffect(() => {
    countPV().then((res) => {
      console.log('总PV:', res);
      setAllPVCount(res);
    });
    countComments().then((res) => {
      console.log('总评论:', res);
      setAllCommentCount(res);
    });
    setTimeout(() => {
      countlatestDaysPV().then((res) => {
        console.log('7日PV:', res);
        setPVsCount(res.filter(v => v));
      });
    }, 100);
    setTimeout(() => {
      countLatestDaysComments().then((res) => {
        console.log('7日评论:', res);
        setCommentsCount(res.filter(v => v));
      });
    }, 500);
  }, [refreshKey]);

  const refresh = () => {
    setRefreshKey(Math.random())
  };

  return (
    <div className='dashboard-wrapper'>
      <div className='title'>
        <h3>后台数据 - 中国的中古</h3>
      </div>
      <div className='overview'>
        <div className='line'>
          总PV: <span className='count'>{allPVCount}</span>
        </div>
        <div className='line'>
          总评论数: <span className='count'>{allCommentCount}</span>
        </div>
        <div className={classes.refreshBtn}>
          <IconButton aria-label='delete' onClick={refresh}>
            <RefreshIcon />
          </IconButton>
        </div>
      </div>

      <div className='chart-block latest-7-days-pv'>
        <h3>最近7天PV数据</h3>
        <div className='desc'>
          <p>PV: Page View。即页面浏览量或点击量。</p>
        </div>
        <LineChart
          data={{ source: pvsCounts }}
          source={pvsCounts}
          position='date*pv'
          width='100%'
          height='300px'
          alias={{
            y: '日PV',
          }}
        />
      </div>

      <div className='chart-block latest-7-days-pv'>
        <h3>最近7天评论数据</h3>
        <LineChart
          data={{ source: commentsCount }}
          source={commentsCount}
          position='date*count'
          width='100%'
          height='300px'
          alias={{
            y: '日评论',
          }}
        />
      </div>
    </div>
  );
}
