import dayjs from 'dayjs'


/**
 * 获取最近N天的数据（涵盖今天）。默认为7天
 * @returns 
 */
export function latestNDays(n = 7) {
  const days = [];

  for(let i = n - 1; i>= 0; i--) {
    const date = dayjs().subtract(i, 'day')
    days.push({
      start: date.startOf('date'),
      end: date.endOf('date'),
    })
  }

  return days;
}