import React, { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import dayjs from 'dayjs';

function Copyright() {
  return (
    <Typography variant='body2' color='textSecondary' align='center'>
      {'Copyright © '}
      <Link color='inherit' href='http://medieval-china.github.io/'>
        Medieval-China
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

function Alert(props) {
  return <MuiAlert elevation={6} variant='filled' {...props} />;
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignIn(props) {
  const classes = useStyles();
  const [snackbar, setSnackbar] = useState({
    open: false,
    severity: 'warning',
    message: '',
  });
  const [userName, setUserName] = useState();
  const [password, setPassword] = useState();

  const handleSubmit = (e, values) => {
    e.preventDefault();
    if (!userName) {
      setSnackbar({ open: true, severity: 'warning', message: '请输入用户名' });
      return;
    }
    if (!password) {
      setSnackbar({ open: true, severity: 'warning', message: '请输入密码' });
      return;
    }

    const nowPw = dayjs().format('YYYYMMDD');
    if (userName === 'admin' && password === nowPw) {
      setSnackbar({ open: true, severity: 'success', message: '登录成功～' });
      props.onLogin && props.onLogin();
    } else {
      setSnackbar({ open: true, severity: 'warning', message: '账号名或密码错误' });
    }
  };

  const handleClose = () => {
    setSnackbar({ open: false, severity: snackbar.severity, message: '' });
  };

  return (
    <Container component='main' maxWidth='xs'>
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component='h1' variant='h5'>
          Sign in
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          <TextField
            variant='outlined'
            margin='normal'
            required
            fullWidth
            id='username'
            label='User Name'
            name='username'
            autoComplete='username'
            placeholder='admin'
            onChange={(e) => setUserName(e.target.value)}
            autoFocus
          />
          <TextField
            variant='outlined'
            margin='normal'
            required
            fullWidth
            name='password'
            label='Password'
            type='password'
            id='password'
            autoComplete='current-password'
            onChange={(e) => setPassword(e.target.value)}
          />
          {/* <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          /> */}
          <Button type='submit' fullWidth variant='contained' color='primary' className={classes.submit}>
            Sign In
          </Button>
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>

      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        autoHideDuration={1500}
        open={snackbar.open}
        onClose={handleClose}
        message={snackbar.message}
      >
        <Alert onClose={handleClose} severity={snackbar.severity}>
          {snackbar.message}!
        </Alert>
      </Snackbar>
    </Container>
  );
}
