import React, { PureComponent } from 'react';
import Chart from './Chart';

const defaultStyle = {
  width: '100%',
  height: '100%',
};
export default class LineChart extends PureComponent {
  creator = ({ chart, data, alias }) => {
    const { source, position } = data;
    const [xKey, yKey] = position.split('*');

    chart.source(source, {
      [xKey]: {
        type: 'timeCat',
        mask: 'MM/DD',
        tickCount: 7,
        range: [0, 1],
      },
      [yKey]: {
        tickCount: 6,
        min: 0,
        alias: alias.y,
      },
    });

    chart.tooltip({
      showCrosshairs: true,
    });

    chart.line().position(position).shape('smooth').color('#3da742');
    chart.point().position(position).shape('smooth').color('#3da742').style({
      stroke: '#fff',
      lineWidth: 1,
    });

    chart.render();
  };

  render() {
    const { source = [], position, width, height, alias = {} } = this.props;
    const style = {
      width: width || defaultStyle.width,
      height: height || defaultStyle.height,
    };

    return (
      <div className='jsChartWrap' style={style}>
        <Chart data={{ source, position, alias }} creator={this.creator} />
      </div>
    );
  }
}
